import { Phaser } from '../base/globalVar';
// import { game } from './game';
import * as http from '../utils/http';
window['global_temp_interval_timer'] = [];
window['global_temp_settimeout_timer'] = [];

window.addEventListener('error', function (msg, url, line, word, e) {

    let msgJson = {
        msg,
        url,
        line,
        word,
        message: e ? e.message : "",
        stack: e ? e.stack : "",
        loadError: false
    };

    http.post(JSON.stringify(msgJson), 'https://logging.abctime-me.com/abctime/logging');

})


const boot = {
    init(data) {

    },
    preload() {

        this.stage.disableVisibilityChange = true;
        this.scale.pageAlignHorizontally = true;
        this.scale.pageAlignVertically = true;
        this.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;


    },
    create() {

        this.state.start('preload');
    }
};


export { boot };


