
import { Phaser } from '../globalVar';
import { timerClock } from '../../animations/timerClock';
import { message } from '../../utils/message';
import { game } from '../../state/game';

let read = function (game, item, second) {
    message('跟读题开始答题');

    try {
        timerClock(second, game, () => {
            console.log('jisu')

        });
        item['twinkleAni'] && item['twinkleAni'].play(2, true);
        item['mouthLowAni'] && item['mouthLowAni'].play(2, true);
        item['starFlag'] = true;


        for (let i = 0; i < item['readGroup']['children'].length; i++) {
            let tw = game.add.tween(item['readGroup']['children'][i]);
            tw.to({
                alpha: 1
            }, 300, Phaser.Easing.Linear.None, true);
        }
        let myss = game.add.tween(item['readGroup']['scale']);

        myss.from({
            x: 0.8,
            y: 0.8
        }, 300, Phaser.Easing.Linear.None, true);

        //

        let bfff = game.add.tween(item['barGroup']['scale']);
        bfff.from({
            x: 0.8,
            y: 0.8
        }, 300, Phaser.Easing.Linear.None, true);



        let read_bar_bg = game.add.tween(item['read_bar_bg']);
        read_bar_bg.to({
            alpha: 1
        }, 300, Phaser.Easing.Linear.None, true);

        let read_star_big = game.add.tween(item['read_star_big']);
        read_star_big.to({
            alpha: 1
        }, 300, Phaser.Easing.Linear.None, true);
    } catch (e) {

    }

    return {
        type: item['type'],
        read_type: item['read_type'],
        read_content: item['read_content']
    };
}

export {
    read
}


