let dragShowAnswer = function (game, Phaser, groupA, groupB, isCation) {

    try {

        let arrA = groupA['children'];
        let arrB = groupB['children'];
        let len = arrB.length;

        if (isCation) {


            for (let i = 0; i < len; i++) {
                if (arrA[i]) {

                    let answerArr = arrA[i]['temName'];

                    for (let j = 0; j < answerArr.length; j++) {

                        if (arrB[answerArr[j]]) {
                            let temB = arrB[answerArr[j]];

                            game.add.tween(temB).to({
                                x: arrA[i]['position']['x'],
                                y: arrA[i]['position']['y'],
                            }, 300, Phaser.Easing.Linear.None, true, 0, 0, false).onComplete.add(function () {

                                game.add.tween(temB).to({
                                    alpha: 0
                                }, 300, Phaser.Easing.Linear.None, true, 0, 0, false);

                            }, this);
                        }


                    }
                }

            }

        } else {


            for (let i = 0; i < len; i++) {
                if (arrA[i]) {

                    let temB = arrB[arrA[i]['temName']];

                    game.add.tween(temB).to({
                        x: arrA[i]['position']['x'],
                        y: arrA[i]['position']['y'],
                    }, 300, Phaser.Easing.Linear.None, true, 0, 0, false);
                }


            }

        }

    } catch (e) {

    }
}

export {
    dragShowAnswer
}


