import { goToState } from './stateManager';
import { data } from '../base/globalVar';
import { game } from './game';
import { showLoading } from '../utils/showLoading';
import { totalAssets } from '../utils/totalAssets';
import { courseLoaded } from '../base/callNativeFunction';
import * as http from '../utils/http';
import { message } from '../utils/message';

const preload = {
    init() {

    },
    preload() {
        changeRem();
        let myResize = 0;
        let myResizeTimer = setInterval(function () {
            myResize += 1;
            if (myResize > 50) {
                clearInterval(myResizeTimer);
            }
            changeRem();

        }, 100);

        // console.log(data)
        if (data['page_size'] && data['page_size']['w'] === 1024) {
            let mydom = document.getElementById('root');
            mydom.style.height = '100%';

        }

        showLoading(true);
        let courseware = data && data['courseware'] ? data['courseware'] : [];
        console.log(courseware)
        //计算资源数量
        // totalAssets(courseware);

        game.load.onLoadComplete.add(function () {

            window['load_success_flag'] = true;


            showLoading(false);
            try {
                courseLoaded({ "status": 1 });
                message('<div style="color:orange"> 通知原生课件加载</div>')

            } catch (e) {
                console.log(e);
            }

            try {
                window.parent.courseloaded();


            } catch (e) {

            }

            clearTimeout(window['refreshtimer']);
            clearTimeout(window['loadTimeoutTimer']);

        }, this);


        game.load.onFileComplete.add(function (progress, cacheKey, success, totalLoaded, totalFiles, a) {
            // console.log(progress);
            let dom = document.querySelector('.abc_time_loading_is_loading .abc_time_loading_fail');
            // console.log(dom);
            dom.innerHTML = '课件加载中...' + progress + '%';

        }, this);


        reloadAssert();
        //加载资源
        totalAssets(courseware, game);
    },
    create() {

        /**
         * 需要资源加载完全调用
         */

        goToState(0);
    }
};

export {
    preload
};




function changeRem() {
    document.getElementsByTagName('html')[0].style.fontSize = window.innerWidth / 1280.0 * 100 + 'px';
}


function reloadAssert() {
    game.load.onFileError.add(function (cacheKey, obj) {
        console.log(cacheKey);
        needReloaHtml();
        showLoading(true, true);
        // console.log(obj);
        http.post(JSON.stringify({
            message: obj['url'] + '加载失败',
            url: obj['url'],
            loadError: true,
            location: window.location.href
        }), 'https://logging.abctime-me.com/abctime/logging');

        message('<div style="color:red">' + obj['url'] + '加载失败' + '</div>');

        if (obj['type'] === 'image') {

            game.load.image(obj['key'], obj['url']);
            console.error('重新加载...' + obj['url']);
        } else if (obj['type'] === 'spritesheet') {
            game.load.spritesheet(obj['key'], obj['url'], obj['frameWidth'], obj['frameHeight'], obj['frameMax']);
            console.error('重新加载..spritesheet.' + obj['url']);
        }


    }, this)
}

function needReloaHtml() {

    var failloading = document.querySelector('.abc_time_loading_fail_loading');
    var isloading = document.querySelector('.abc_time_loading_is_loading');
    failloading.style.display = 'block';
    isloading.style.display = 'none';
}



